import React, { PureComponent } from "react";
import { connect } from "react-redux";
// import { isMobile } from 'react-device-detect';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { Grid } from '@splidejs/splide-extension-grid';
import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../actions/themeActions'
import '../scss/ThemesSplide.scss'
class HorizontalSlider extends PureComponent {
  constructor(props) {
    super(props);
    // this.inputRefs = React.createRef();
    this.state = { parent_id: '' }
    this.inputRefs = {};
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
  }
  componentDidUpdate() {
    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, wall, parentID } = this.props;
    const rowCount = wall.ThemeRule.multiRow;
    if (value && Object.values(value)[5].length > 0) {
      let data = Object.values(value)[5]
      if (rowCount == 1) {
        data.map((item, itemIndex) => {
          if (item.classList) {
            const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
            var getID = filterID[0]
            if (String(filterID[0]).includes("tb_")) {
              getID = String(filterID[0]).replace("tb_", "")
            }
            const findIndex = postData.findIndex((find) => find == getID)
            // let parentId = Object.keys(this.inputRefs)[0]
            let clickD = document.querySelectorAll(`#${item.id}`)
            for (let index = 0; index < clickD.length; index++) {
              let clickDD = clickD[index];
              let idd = clickDD.getAttribute('idd')
              // console.log('idd:, parentID', idd, parentID);
              if (String(parentID) === String(idd)) {
                // console.log("parentID, clickD", parentID, clickDD)
                clickDD.addEventListener('click', event => {
                  event.preventDefault();
                  // console.log("from__clickDD==> parentID, clickD", parentID, clickDD)
                  const itemData = completeDataObject[getID]
                  // console.log('findIndex, itemData', findIndex, itemData);
                  onClickPopUpSlider(findIndex, itemData)
                });
              }
            }
          }
        })
      }
      else if (rowCount == 2) {
        let data = Object.values(value)[5]
        data.map((item, itemIndex) => {
          if (item && item.childNodes.length > 0) {
            item.childNodes.forEach((childItem) => {
              if (childItem && childItem.childNodes.length > 0) {
                childItem.childNodes.forEach((subChildItem) => {
                  if (subChildItem.classList) {
                    const filterID = String(subChildItem.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
                    var getID = filterID[0]
                    if (String(filterID[0]).includes("tb_")) {
                      getID = String(filterID[0]).replace("tb_", "")
                    }
                    const findIndex = postData.findIndex((find) => find == getID)
                    // console.log("subChildItem.id", subChildItem);
                    let clickD = document.querySelectorAll(`.tb_hs_post_container #${subChildItem.id}`)
                    // console.log('clickD:', clickD);
                    for (let index = 0; index < clickD.length; index++) {
                      let clickDD = clickD[index];
                      let idd = clickDD.getAttribute('idd')
                      // console.log('idd:,parentID ', idd, parentID);
                      if (String(parentID) === String(idd)) {
                        clickDD.addEventListener('click', event => {
                          event.preventDefault();
                          // console.log("from__clickDD==> parentID, clickD", parentID, clickDD)
                          const itemData = completeDataObject[getID]
                          // console.log('findIndex, itemData', findIndex, itemData);
                          onClickPopUpSlider(findIndex, itemData)
                        });
                      }
                    }
                  }
                })
              }
            });


          }

        })
      }

    }
  }
  requestData = () => {
    const { appendData, wall, preRender, hasMoreData } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, onClickToCTA, postSizeInPx, parentID } = this.props;
    const inputName = parentID;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const rowCount = wall.ThemeRule.multiRow;

    const settings = {

      grid: {
        rows: rowCount,
      },
      type: autoScrollStatus === 1 ? false : 'loop',
      rewind: false,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : 6,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      arrows: true,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 4,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 6,
        }
      },
    };
    return (<>{parentID != "" ? <div className="tb_hs_post_container">
      <Splide hasTrack={false} className="tb_hs_post_slider" options={settings} extensions={{ Grid }} ref={(parentID) => this.inputRefs[inputName] = parentID}>
        <SplideTrack>
          {
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              return <SplideSlide style={{ margin: 0, padding: 0 }} key={index} className={`tb_${cardData.id}`} idd={parentID}>
                <Card parentID={parentID} ownerId={wall.Wall.owner} wall={wall} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} />
              </SplideSlide>
            })}
        </SplideTrack>
        <div className="splide__arrows splide__arrows--ltr tb_hs_arrow_wrapper_">
          <div className="tb_hs_arrow splide__arrow splide__arrow--prev tb_hs_arrow tb_hs_arrow_left__ tb__icon tb-arrow-left-alt" > </div>
          <div className="tb_hs_arrow splide__arrow splide__arrow--next  tb_hs_arrow_right__ tb__icon tb-arrow-right-alt" onClick={this.requestData}></div>
        </div>
      </Splide>
    </div> : null}</>)
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(null, mapDispatchToProps)(HorizontalSlider);