import React, { PureComponent } from "react";
import { getImageSize } from 'react-image-size';
import { IMG_PLACEHOLDER } from "../../../../constants";
import ReactPlayer from "react-player"

const videoIframeTyle = ["youtube", "youtu.be", "vimeo", "soundcloud"]

const isVideo = (videoLink) => {
    let isVideoStatus = false;
    if (String(videoLink)) {
        videoIframeTyle.map((item) => {
            if (String(videoLink).includes(item) && !isVideoStatus) isVideoStatus = true;
        })
    }
    return isVideoStatus;

}
export default class Video extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            imgUrl: this.props.imgUrl,
            newSize: {
                height: 300,
                width: 300
            },
            mediaSize: {
                height: 100,
                width: 100
            },
            videoLoaded:false
        }
        this.VideoWrap = React.createRef();
    }

    componentDidMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }
        setTimeout(() => {
            this.updateDimensions();
        }, 100);
        if (!this.props.size) {
            this.onMediaLoad();
        }
    }
    updateDimensions = () => {
        const { size } = this.props
        let width = this.VideoWrap.current.getBoundingClientRect().width;
        if (width) {
            this.setState({
                newSize: {
                    height: ((width * size.height) / size.width).toFixed(),
                    width: width.toFixed()
                }
            });
        }else{
            this.setState({
                newSize: {
                    height: 300,
                    width: 300
                }
            });
        }
    };

    onMediaLoad = async (props) => {
        const { data } = this.props;
        const { imgUrl } = this.state;
        if (data && Object.keys(data).length > 0 && [4, 5, 3].includes(data.type)) {
            if (data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "") {
                this.setState({
                    mediaSize: {
                        height: data.mediaHeight,
                        width: data.mediaWidth,
                    }
                })
            } else {
                try {
                    const { width, height } = await getImageSize(imgUrl);
                    this.setState({
                        mediaSize: {
                            height: height,
                            width: width,
                        }
                    })
                } catch (error) {
                    this.setState({
                        loadError: true,
                        mediaSize: {
                            height: 100,
                            width: 100
                        }
                    })
                }
            }
        }
    }

    onLoad = event => {
        if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
    }

    setVideoLoaded = event => {
        this.setState({
            videoLoaded:event
        })
    }

    render() {
        const { VideoClass, data, wallID, isCover, size } = this.props
        const { imgUrl, newSize, mediaSize, videoLoaded } = this.state
        const imageStyle = {
            paddingBottom: size ? `${size}%` : (mediaSize.height * 100) / mediaSize.width + '%'
        };

        return  <div className={`${VideoClass}_wrap_ mediaHolder${data.id}`} style={imageStyle} ref={this.VideoWrap}>
                    <ReactPlayer 
                        // ref={videoRef}
                        className={VideoClass}
                        url={data.mediaUrl}
                        data-type="video"
                        data-network={data.networkId}
                        data-link={data.link}
                        data-wall-id={wallID} 
                        data-item-id={data.id}
                        data-load={0}
                        config={{
                            facebook: {
                                appId: '117029975354072',
                                attributes: {
                                    'data-width': 'auto',
                                    // 'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                                }
                            },
                            // tracks: [
                            //     {kind: 'subtitles', src: `${CloudUrl}/media/images/subtitles.en.vtt`, srcLang: 'en', default: true},
                            // ]
                        }}
                        loop={true}
                        onError={(e) => { this.setVideoLoaded(false) }}
                        //(data.stories == 0) ? POPUP_VIDEO_RENEW(e) : null}
                        autoPlay={true}
                        muted={true}
                        volume={0}
                        playsinline={true}
                        onReady={(e) => {this.setVideoLoaded(true)}}
                        playing={true}
                        height={'100%'}
                        width={'100%'}
                        controls={false}
                        // onProgress={onPlayingVideo}
                        style={{ backgroundImage:(!videoLoaded) ? `url(${data.postFileNew})` : '', backgroundSize:isCover ? 'cover' : 'contain', backgroundPosition:'center', backgroundRepeat:'no-repeat' }}
                    />
                </div>
    }
}

