import React, { PureComponent } from "react";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
export default class Media extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: props.itemData.postFileNew
    }
  }

  render() {
    const { itemData, wallID, ownerId, wall } = this.props;
    const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
    const isYoutubePost = (itemData.network.id === 7) ? true : false
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_sp_multi_images_ico_bottom' : 'tb_hs_multi_images_ico'
    const isAudio = itemData.isAudio === 1 ? true : false
    const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
    return (
      <div className="tb_hs_media_wrap">
        {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
        {mediaType && !autoPlay ? isYoutubePost ? <div className="tb_hs_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div> :
          isAudio ? <div className="tb_hs_audio_ico tb__icon tb-audio"><div></div></div> :
            <div className="tb_hs_video_ico tb__icon tb-video"><div></div></div> : ''}
            {autoPlay ?
              <Video VideoClass="tb_hs_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={100} isCover={true} />
            :
              <Image ImageClass="tb_hs_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={100} />
            }
      </div>
    );
  }
}